.bg {
    /* The image used */
    background-image: url("https://upload.wikimedia.org/wikipedia/commons/thumb/a/af/KPRIET.jpg/800px-KPRIET.jpg");
  
    /* Full height */
    height: 550px;
    /* Center and scale the image nicely */
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }
  .container {
    width: 80%;
    margin: auto;
    overflow: hidden;
}